  h4 {
    color: crimson;
    font-size: 17px;
    text-transform: lowercase;
    margin-top: 16px;
    font-weight: 600;
  }
  
  .row#cardgrid {
    display: flex;
  }
  
  .col-md-3 {
    margin-bottom: 40px;
  }    
  
  .album-poster {
    position: relative;
    display: block;
    width: 200px;
    height: 200px;
    min-width: 200px;
    min-height: 200px;
    border-radius: 7px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(235, 99, 189, 0.3);
    transition: all ease .4s;
  }
  /* TEST CARD FLIP */
  
  .flip-card {
    position: relative;
    display: block;
    width: 200px;
    height: 200px;
    min-width: 200px;
    min-height: 200px;
    border-radius: 7px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(235, 99, 189, 0.3);
    transition: all ease .4s;
  
  }
  
  /* TEST CARD FLIP */
  
  // #flip-card-inner {
  //   position: relative;
  //   width: 100%;
  //   height: 100%;
  //   text-align: center;
  //   transition: transform 0.8s;
  //   transform-style: preserve-3d;
  // }
  
  .album-poster:hover {
    box-shadow: none;
    transform: scale(0.89) translateY(5px);
  }
  
  /* .flip-card:hover #flip-card-inner {
    transform: rotateY(180deg);
  } */
  
  
  
  /* Position the front and back side */
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  /* Style the front side (fallback if image is missing) */
  .flip-card-front {
    background-color: #bbb;
    color: black;
  }
  
  /* Style the back side */
  .flip-card-back {
    background-color: dodgerblue;
    color: white;
    transform: rotateY(180deg);
  }
  
  
  /* END FLIP CARD TEST */
  
  img {
    justify-content: center;
    max-width: 250px;
    max-height: 250px;
    min-width: 200px;
    min-height: 200px;
    border-radius: 7px;
    transform: translateZ(20px);
  }  
  
  
  h3 {
